.sidebarOption{
    display: flex;
    align-items: center;
    height: 35px;
    padding: 0 10px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    /* background-color: gray; */
    cursor: pointer;
    color: #818181;
}

.sidebarOption > ,MuiSvgIcon-root{
    padding: 5px;
}

.sidebarOption >h3{
    flex: 1;
    margin-left: 10px;
    font-size: 14px;
    font-weight: 400;
}

.sidebarOption >p{
    /* display: none; */
    font-weight: 300;
}

.sidebarOption__active{
    background-color: #fcecec;
    color: #c04b37;
}

.sidebarOption :hover > p{
    display: inline;
}

.sidebarOption:hover,
.sidebarOption:hover >p,
.sidebarOption:hover>h3{
    background-color: #fcecec;
    color: #c04b37;
    /* font-size: medium; */
    /* font-weight: 800 !important; */
}